import React, { useState, useEffect } from "react"
import { Modal, Box, Typography, IconButton, Button } from "@mui/material"
import CloseIcon from "@mui/icons-material/Close"
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart"
import helper from "../../Resources/Class/helper"
import { useNavigate } from "react-router-dom"
import ProductCarrusel from "../Carrusel/ProductCarrusel/productCarrusel"

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "90%",
  maxWidth: 500,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 2,
}

const ModalInfo = ({ open, handleClose, product }) => {
  const [selectedImage, setSelectedImage] = useState(null)
  const navigate = useNavigate()

  useEffect(() => {
    if (product && product.images?.length > 0) {
      setSelectedImage(product.images[0])
    } else {
      setSelectedImage(null)
    }
  }, [product])

  const handleSelect = (product) => {    
    navigate(`/home/buys/${product.id}`)
    handleClose()
  }

  return (
    <Modal open={open} onClose={handleClose} sx={{ mx: { xs: 4 } }}>
      <Box sx={style}>
        {product ? (
          <>
            <Box display="flex" justifyContent="space-between" mb={2}>
              <Typography variant="h6">{product.name}</Typography>
              <IconButton onClick={handleClose}><CloseIcon /></IconButton>
            </Box>
            {selectedImage && ( <Box component="img" src={selectedImage} alt="Product" sx={{ width: '100%', borderRadius: 1 }} /> )}

            <ProductCarrusel
              product={product}
              selectedImage={selectedImage}
              onSelectImage={setSelectedImage}
              customBreakpoints={{
                375:   { slidesPerView: 3 },                
                600:   { slidesPerView: 4 }                
              }}
            />
            <Typography variant="body1" mt={2}>{product.description}</Typography>
            <Typography variant="h6" mt={1}>${helper.formatPrice(product.price)}</Typography>
            <Button variant="contained" color="primary" fullWidth sx={{ mt: 2 }} startIcon={<ShoppingCartIcon />} onClick={() => handleSelect(product)}>
              Comprar ahora
            </Button>
          </>
        ) : (
          <Typography variant="body2" color="textSecondary">Cargando informaci√≥n del producto...</Typography>
        )}
      </Box>
    </Modal>
  )
}

export default ModalInfo
