import React, { useState } from "react"
import {Card,CardActions,CardContent,CardMedia,Typography,IconButton,Box} from "@mui/material"
import { Link } from 'react-router-dom'
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart"
import AttachMoneyIcon from "@mui/icons-material/AttachMoney"
import FavoriteIcon from '@mui/icons-material/Favorite'
import SearchIcon from "@mui/icons-material/Search"
import ModalInfo from '../Modal/ModalInfo'
import Helper from '../../Resources/Class/helper'
import Utils from '../../Resources/Class/utils'
import Server from '../../Resources/Json/Endpoints.json'
import { useUser } from '../../Context/Provider'

const ProductCard = ({ product, setIsLoading, setSuccess, setError }) => {
  const { token } = useUser()
  const [openModal, setOpenModal] = useState(false)
  const handleOpenModal = () => setOpenModal(true)
  const handleCloseModal = () => setOpenModal(false)

  const handleUserProduct = async (event, params, type) => {
    event.preventDefault()
    setIsLoading(true)
    Helper.axiosRequest({
      method: "POST",
      url: Server.endpoints.createUserProduct,
      data: { idProduct: params.id, quantity: params.quantity, status: type },
      headers: { Authorization: token.isToken || token }
    })
    .then(data => { Utils.success(data, setSuccess)  })
    .catch(error => { Utils.error(error, setError) })
    .finally(() => { setIsLoading(false) })
  }

  return (
    <>
      <Card className="card">
        <CardMedia
          component="img"
          alt={product.name}
          image={product.image}/>

        <CardContent>
          <Typography
            gutterBottom
            variant="h6"
            component="div"
            className="product-title">
            {product.name}
          </Typography>
          <Typography variant="body2" color="text.secondary">{product.description}</Typography>
          <Typography variant="h6" className="product-price">${Helper.formatPrice(product.price)}</Typography>
        </CardContent>

        <Box className="icon-button-container">
          <IconButton
            aria-label="A√±adir a favoritos"
            color="secondary"
            onClick={event => handleUserProduct(event, product, 'FAVORITES')}>
            <FavoriteIcon className="icon-button" sx={{stroke: 'red',strokeWidth: '2px',fill: 'white','&:hover': {fill: 'red',}}} />
          </IconButton>
          <Typography variant="body2" color="text.secondary">A√±adir a favoritos</Typography>
        </Box>

        <CardActions className="card-actions">
          <Box className="icon-button-container">
            <IconButton
              aria-label="A√±adir al carrito"
              color="primary"
              onClick={event => handleUserProduct(event, product, 'CART')}>
    
              <AddShoppingCartIcon className="icon-button" />
            </IconButton>
            <Typography variant="body2" color="text.secondary">A√±adir al carrito</Typography>
          </Box>

          <Box className="icon-button-container">
            <IconButton
              aria-label="Ver m√°s"
              color="primary"
              onClick={handleOpenModal}>
              <SearchIcon className="icon-button" />
            </IconButton>
            <Typography variant="body2" color="text.secondary">Ver m√°s</Typography>
          </Box>

          <ModalInfo
            open={openModal}
            handleClose={handleCloseModal}
            product={product} />

          <Box className="icon-button-container" component={Link} to={`/home/buys/${product.id}`} sx={{ textDecoration: 'none' }}>
            <IconButton aria-label="Comprar ahora" color="primary">
              <AttachMoneyIcon className="icon-button" />
            </IconButton>
            <Typography variant="body2" color="text.secondary">Comprar ahora</Typography>
          </Box>
        </CardActions>
      </Card>
    </>
  )
}

export default ProductCard
