import React from 'react'
import {Table,TableBody,TableCell,TableContainer,TableHead,TableRow,Paper,IconButton,Typography,Button,Box,Grid,TablePagination,Breadcrumbs,Link,TextField,Accordion,AccordionSummary,AccordionDetails,LinearProgress} from '@mui/material'
import { Delete, ShoppingCart, Payment, CheckCircle, LocalShipping, VerifiedUser, ExpandMore, LocalOffer, AttachMoney, ShoppingBag, Calculate, Settings, NavigateNext, Home, Storefront } from '@mui/icons-material'
import { motion } from 'framer-motion'
import Helper from '../../Resources/Class/helper'
import useCart from '../Cart/useCart'
import CardCarrusel from '../../Components/Carrusel/CardCarrusel/CardCarrusel'

const Cart = () => {
  const { state, handlers, context } = useCart()

  return (
    <Box sx={{ px: 2, py: 4 }}>      
      <Breadcrumbs aria-label="breadcrumb" separator={<NavigateNext fontSize="small" sx={{ color: '#00004e' }} />}
        sx={{ mb: 2, padding: '8px 16px', borderRadius: 2, color: '#00004e', fontFamily: 'Orbitron, sans-serif' }}>
        <Link href="/home" underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
          <Home sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Inicio
        </Link>
        <Link href="/home/products" underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
          <Storefront sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Productos
        </Link>
        <Typography sx={{ display: 'flex', alignItems: 'center', fontWeight: 'bold', color: '#00004e' }}>
          <ShoppingCart sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Carrito
        </Typography>
      </Breadcrumbs>

      <Paper elevation={3} sx={{ p: 2, mb: 3 }}>
        <Box display="flex" flexDirection="column" gap={1}>
          <Typography variant="h6" gutterBottom sx={{ fontSize: { xs: '0.9rem', sm: '1.25rem' }, fontWeight: 'bold', color: '#00004e' }}>
            {state.total >= state.FREE_SHIPPING_THRESHOLD
              ? '¬°Felicitaciones! Calific√°s para el env√≠o gratis üéâ'
              : `Agreg√° $${Helper.formatPrice(state.FREE_SHIPPING_THRESHOLD - state.total)} m√°s para env√≠o gratis`}
          </Typography>

          <motion.div style={{ width: '100%' }}>
            <LinearProgress
              variant="determinate"
              value={state.progressValue}
              sx={{
                height: 15,
                borderRadius: 10,
                backgroundColor: '#f0f0f0',
                '& .MuiLinearProgress-bar': {
                  backgroundColor: state.total >= state.FREE_SHIPPING_THRESHOLD ? '#4caf50' : '#ff9800',
                  transition: 'width 0.3s ease-in-out',
                }
              }}
            />
          </motion.div>
        </Box>
      </Paper>

      <Grid container spacing={4}>
        <Grid item xs={12} md={8}>
          <TableContainer ref={state.tableRef} component={Paper} sx={{ boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.2)' }}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>
                    <Box display="flex" flexDirection="column" alignItems="center"> <ShoppingBag sx={{ marginRight: 1 }} /> Producto </Box>
                  </TableCell>
                  {!state.isMobile && (
                    <TableCell>
                      <Box display="flex" flexDirection="column" alignItems="center"> <AttachMoney sx={{ marginRight: 1 }} /> Precio Unitario </Box>
                    </TableCell>
                  )}
                  <TableCell>
                    <Box display="flex" flexDirection="column" alignItems="center"> <LocalOffer sx={{ marginRight: 1 }} /> Cantidad </Box>
                  </TableCell>
                  {!state.isMobile && (
                    <TableCell>
                      <Box display="flex" flexDirection="column" alignItems="center"> <Calculate sx={{ marginRight: 1 }} /> Subtotal </Box>
                    </TableCell>
                  )}
                  <TableCell>
                    <Box display="flex" flexDirection="column" alignItems="center"> <Settings sx={{ marginRight: 1 }} /> Acciones </Box>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
              {state.paginatedItems.map((item, index) => (
                <TableRow key={item.id} sx={{ backgroundColor: index % 2 === 0 ? '#ffffff' : '#f5f5f5' }}>
                  <TableCell>
                    <Box display="flex" alignItems="center" justifyContent="center">
                      <Box component="img"
                        src={item.image}
                        alt={item.name}
                        sx={{ width: 50, height: 50, marginRight: 1 }}/>
                    </Box>
                  </TableCell>
                  {!state.isMobile && (
                    <TableCell>
                      <Box display="flex" alignItems="center" justifyContent="center"> ${Helper.formatPrice(item.price)} </Box>
                    </TableCell>
                  )}
                  <TableCell align="center">
                    <Box display="flex" alignItems="center" justifyContent="center">
                      <TextField
                        value={state.quantities[item.id]}
                        variant="outlined"
                        size="small"
                        inputProps={{
                          style: { textAlign: 'center', width: 40, height: 30 },
                        }}
                        sx={{ mx: 1 }}
                        readOnly/>
                      <Box display="flex" flexDirection="column" alignItems="center">
                        <Button variant="outlined"
                          color="primary"
                          size="small"
                          onClick={() => handlers.handleQuantityChange(item.id, state.quantities[item.id] + 1)}
                          sx={{ minWidth: 30, padding: 0 }}>
                          +
                        </Button>
                        <Button variant="outlined"
                          color="primary"
                          size="small"
                          onClick={() => handlers.handleQuantityChange(item.id, state.quantities[item.id] - 1)}
                          disabled={state.quantities[item.id] === 1}
                          sx={{ minWidth: 30, padding: 0, mt: 0.5 }}>
                          -
                        </Button>
                      </Box>
                    </Box>
                  </TableCell>
                  {!state.isMobile && (
                    <TableCell>
                      <Box display="flex" alignItems="center" justifyContent="center"> ${Helper.formatPrice(item.price * state.quantities[item.id])} </Box>
                    </TableCell>
                  )}
                  <TableCell>
                    <Box display="flex" alignItems="center" justifyContent="center">
                      <IconButton color="error" onClick={() => handlers.handleRemove(item.id)}> <Delete /> </IconButton>
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
              rowsPerPageOptions={[5, 10, 15]}
              component="div"
              count={state.cartItems.length}
              rowsPerPage={state.rowsPerPage}
              page={state.page}
              onPageChange={handlers.handleChangePage}
              onRowsPerPageChange={handlers.handleChangeRowsPerPage}
              labelRowsPerPage="Filas por p√°gina:"
              labelDisplayedRows={({ from, to, count }) => `${from}-${to} de ${count}`}/>
        </Grid>

        <Grid item xs={12} md={4} sx={{
            position: { xs: 'static', md: 'sticky' },
            top: { md: 70 },
            alignSelf: 'start',
          }}>
          <Paper elevation={3} sx={{ p: 2 }}>
            <Box display="flex" mb={1}>
              <Typography variant="h6" gutterBottom my={0}> Resumen del carrito </Typography>
              <ShoppingCart sx={{ my: 'auto', mx: 1 }}/>
            </Box>

            <Box my={2}>
              <Accordion sx={{ boxShadow: 'none', border: '1px solid #e0e0e0', '&::before': { display: 'none' }}}>
                <AccordionSummary expandIcon={<ExpandMore />}>
                  <Box display="flex" alignItems="center" gap={1}>
                    <Payment />
                    <Typography variant="body1">
                      M√©todos de Pago ({state.selectedPayment ? 1 : 0}/1)
                    </Typography>
                  </Box>
                </AccordionSummary>
                <AccordionDetails>
                  <Grid container spacing={1}>
                    {state.paymentMethods.map((method) => (
                      <Grid item xs={3} sm={6} md={6} lg={3} key={method.id}>
                        <Box
                          onClick={() => state.setSelectedPayment(method.id)}
                          sx={{
                            cursor: 'pointer',
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                            padding: 1,
                            borderRadius: 1,
                            backgroundColor: state.selectedPayment === method.id ? '#e0f7fa' : 'transparent',
                            '&:hover': { backgroundColor: '#e0f2f1' },
                          }}
                        >
                          {method.icon}
                          <Typography variant="caption" align="center">{method.label}</Typography>
                        </Box>
                      </Grid>
                    ))}
                  </Grid>
                </AccordionDetails>
              </Accordion>

              <Accordion sx={{ mt: 2, boxShadow: 'none', border: '1px solid #e0e0e0', '&::before': { display: 'none' }}}>
                <AccordionSummary expandIcon={<ExpandMore />}>
                  <Box display="flex" alignItems="center" gap={1}>
                    <LocalShipping />
                    <Typography variant="body1">
                      Opciones de Entrega ({state.deliveryOption ? 1 : 0}/1)
                    </Typography>
                  </Box>
                </AccordionSummary>
                <AccordionDetails>
                  <Box display="flex" gap={2} flexWrap="wrap">
                    {state.deliveryMethods.map((method) => (
                      <Box
                        key={method.id}
                        onClick={() => state.setDeliveryOption(method.id)}
                        sx={{
                          cursor: 'pointer',
                          display: 'flex',
                          flexDirection: 'column',
                          alignItems: 'center',
                          padding: 1,
                          borderRadius: 1,
                          backgroundColor: state.deliveryOption === method.id ? '#e0f7fa' : 'transparent',
                          '&:hover': { backgroundColor: '#e0f2f1' },
                        }}
                      >
                        {method.icon}
                        <Typography variant="caption" align="center">{method.label}</Typography>
                      </Box>
                    ))}
                  </Box>
                </AccordionDetails>
              </Accordion>
            </Box>

            <Grid container spacing={2} my={2}>
              <Grid item xs={6} lg={6} display="flex" alignItems="center">
                <Box>
                  <Box
                    display="flex"
                    alignItems="center"
                    gap={1}
                    sx={{
                      color: "success.main",
                      fontWeight: "bold"
                    }}>
                    <CheckCircle />
                    <Typography variant="body1">Stock disponible</Typography>
                  </Box>

                  <Box
                    display="flex"
                    alignItems="center"
                    gap={1}
                    sx={{
                      mt: 1,
                      color: "info.main",
                      fontWeight: "bold"
                    }}>
                    <LocalShipping />
                    <Typography variant="body1">Entrega inmediata</Typography>
                  </Box> 
                </Box>
              </Grid>
              <Grid item xs={6} lg={6} display="flex" justifyContent="end">
                <Box
                  display="flex"
                  flexDirection="column"
                  sx={{
                    mt: 1,
                    color: "primary.main",
                  }}>
                  <Box display="flex" alignItems="center" gap={1}>
                    <VerifiedUser />
                    <Typography variant="body1" fontWeight="bold">
                      Garant√≠a asegurada:
                    </Typography>
                  </Box>

                  <Box sx={{ pl: 4, mt: 0.5 }}>
                    <Typography variant="body2" sx={{ fontSize: { xs: 10, sm: 15 } }}>‚úî Garant√≠a oficial de la marca</Typography>
                    <Typography variant="body2" sx={{ fontSize: { xs: 10, sm: 15 } }}>‚úî Garant√≠a de nuestra tienda</Typography>
                  </Box>
                </Box> 
              </Grid>
            </Grid>

            <Typography variant="body1" gutterBottom>
              Categor√≠as en tu carrito:
            </Typography>

            <Box display="flex" flexWrap="wrap" gap={1} mb={1}>
              {Object.entries(state.categorySummary).map(([category, count]) => (
                <Box
                  key={category}
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    border: '1px solid #ccc',
                    borderRadius: 2,
                    px: 2,
                    py: 1,
                    bgcolor: '#f9f9f9',
                  }}>
                  <Typography variant="body2">
                    {category}: <strong>{count}</strong> producto(s)
                  </Typography>
                </Box>
              ))}
            </Box>

            <Typography variant="body2" gutterBottom>
              Total de productos: <strong>{state.totalItems}</strong>
            </Typography>

            <Typography variant="h6" my={2} sx={{ fontSize: 25, fontWeight: 600, textAlign: 'center' }}>
              Total: ${Helper.formatPrice(state.total)}
            </Typography>

            <Button variant="contained" color="primary" fullWidth startIcon={<Payment />} onClick={() => console.log('Proceder con la compra')}> Finalizar compra </Button>
          </Paper>
        </Grid>
      </Grid>

      <Box>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', mb: 2, mt: 10 }}>A√±ade mas al carrito:</Typography>
        <CardCarrusel 
          cards={context.dataContext}
          setIsLoading={null} 
          setSuccess={null} 
          setError={null}
          customBreakpoints={{
            0: { slidesPerView: 1 },
            700: { slidesPerView: 2 },
            1010: { slidesPerView: 3 },
            1440: { slidesPerView: 4 },
            1750: { slidesPerView: 5 }
          }}/>
      </Box>
    </Box>
  )
}

export default Cart
