import React from 'react'
import { Box } from '@mui/material'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Pagination, Navigation, Autoplay } from 'swiper/modules'
import useCardCarrusel from './useCardCarrusel'
import Card from '../../Card/Card'
import 'swiper/css'
import 'swiper/css/pagination'
import 'swiper/css/navigation'

const CardCarrusel = ({
    cards = [],
    setIsLoading,
    setSuccess,
    setError,
    customBreakpoints
  }) => {

  const { state } = useCardCarrusel()

  return (
    <Box
      sx={{
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        '& .swiper': { width: '100%' },
        '& .swiper-pagination': { mt: 0 },
        '& .swiper-button-next, & .swiper-button-prev': {
            color: '#000',
            backgroundColor: '#fff',
            borderRadius: '50%',
            width: 40,
            height: 40,
            boxShadow: 3,
            '&::after': {
                fontSize: '15px',
                fontWeight: 'bold',
            }
        }
      }}>
      <Swiper
        modules={[Pagination, Navigation, Autoplay]}
        pagination={{ clickable: true }}
        navigation={true}
        autoplay={{
          delay: 5000,
          disableOnInteraction: false
        }}
        slidesPerGroup={state.slidesPerGroup}
        onBreakpoint={(swiper) => {
          const current = swiper.params.slidesPerView
          state.setSlidesPerGroup(typeof current === 'number' ? current : 1)
        }}
        breakpoints={customBreakpoints}
        spaceBetween={16}
        style={{ paddingBottom: '32px' }}>

        {cards.map((card, index) => (
          <SwiperSlide key={index}>
            <Card
              product={card}
              setIsLoading={setIsLoading}
              setSuccess={setSuccess}
              setError={setError}
              sx={{ px: 2, textAlign: 'center' }}>
              Card {card}
            </Card>
          </SwiperSlide>
        ))}
      </Swiper>
    </Box>
  )
}

export default CardCarrusel
