import React from 'react'
import { Box } from '@mui/material'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Pagination } from 'swiper/modules'
import useProductCarrusel from './useProductCarrusel'
import 'swiper/css'
import 'swiper/css/pagination'

const ProductCarrusel = ({ product, selectedImage, onSelectImage, customBreakpoints }) => {
  const { state } = useProductCarrusel()

  return (
    <Box sx={{ width: '100%', mt: 2 }}>
      <Swiper modules={[Pagination]}
        pagination={{ clickable: true }}
        slidesPerGroup={state.slidesPerGroup}
        onBreakpoint={(swiper) => {
          const current = swiper.params.slidesPerView
          state.setSlidesPerGroup(typeof current === 'number' ? current : 4)
        }}
        breakpoints={customBreakpoints}
        style={{ paddingBottom: '32px' }}>

        {(product?.images || []).map((image, index) => (
          <SwiperSlide key={index} style={{ width: 'auto', display: 'flex' }}>
            <Box component="img"
              src={image}
              alt={`Product ${index}`}
              onClick={() => onSelectImage(image)}
              sx={{
                width: 80,
                height: 80,
                mx: 'auto',
                cursor: 'pointer',
                border: selectedImage === image ? '2px solid blue' : '1px solid gray',
                borderRadius: 1,
                objectFit: 'cover'
              }}
            />
          </SwiperSlide>
        ))}
      </Swiper>
    </Box>
  )
}

export default ProductCarrusel
